/* Fix overflow for code snippets */

.code {
    background-color: #011627;
    color: white;
    padding: 20px 0;
    margin: 5px 0;
    border-radius: 20px;
    font-family: Consolas, Monaco, Menlo, Consolas, monospace;
    /* word-wrap: normal; */
    /* display: block; */
    /* width: 100%; */
}

.bg-blue {
    background-color: blue;
}

.c-white {
    color: white;
}

.c-blue {
    color: #82aaff
}

.c-red {
    color: red;
}

.c-pink {
    color: #ffa7c4;
}

.c-green {
    color: #809393;
}

.c-black {
    color: black;
}

.c-orange {
    color: #ffcb8b;
}

.f-bold {
    font-weight: bold;
}

.bd-color {
    border: solid;
    padding: 0 1px;
}



@media screen and (max-width: 480px) {
    .overflowX {
        overflow-x: auto;
    }
}
/* .card-body {
    padding: 0
} */
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 1000;
}

.ion-size {
  font-size: 2rem;
}

.margin-bottom-sm {
  margin-bottom: 10px;
}

.margin-top-xlg {
  margin-top: 80px;
}

.margin-top-lg {
  margin-top: 50px;
}

.margin-top-md {
  margin-top: 20px;
}

.bg-oCard {
  background-color: #f2f9ff;
}

.sticky-OChart {
  position: sticky;
  position: -webkit-sticky;
  top: 50px;
}

.container-margin {
  margin: 20px 0;
}

.divider {
  border-top: solid gray 1px;
}

.placeholder {
  padding-bottom: 60px;
}

.italic {
  font-style: italic;
}


/* Medium devices (landscape tablets, 768px and down) */
@media only screen and (max-width: 768px) {

  .card-body {
    padding: .5rem;
  }

  .App {
    padding: 5px;
  }
}
.bigO-SVGChart {
    height: 100%;
}

.bigOSvg {
    height: 100%;
}
