/* Fix overflow for code snippets */

.code {
    background-color: #011627;
    color: white;
    padding: 20px 0;
    margin: 5px 0;
    border-radius: 20px;
    font-family: Consolas, Monaco, Menlo, Consolas, monospace;
    /* word-wrap: normal; */
    /* display: block; */
    /* width: 100%; */
}

.bg-blue {
    background-color: blue;
}

.c-white {
    color: white;
}

.c-blue {
    color: #82aaff
}

.c-red {
    color: red;
}

.c-pink {
    color: #ffa7c4;
}

.c-green {
    color: #809393;
}

.c-black {
    color: black;
}

.c-orange {
    color: #ffcb8b;
}

.f-bold {
    font-weight: bold;
}

.bd-color {
    border: solid;
    padding: 0 1px;
}



@media screen and (max-width: 480px) {
    .overflowX {
        overflow-x: auto;
    }
}